.label input {
    width: 100%;
    border: 0;
    border-bottom: 3px solid white;
    background: transparent;
    margin-bottom: 25px;
    padding: 8px 0 5px 0;
    font-size: 16px;
    color: white;
    border-radius: 3px;
}

.inputContainer input:focus {
    border: none;
    outline: none;
    border-bottom: 3px solid #ffba12;
}

::placeholder {
    color: white;
    font-size: 16px;
    text-align: start;
}

.btn {
    color: #fff;
    background-color: #ffba12;
    font-size: 16px;
    outline: none;
    border: 0;
    padding: 10px 20px;
    text-transform: uppercase;
    margin: 0 10px 0 10px;
    /*margin-top:50px;*/
    border-radius: 2px;
    cursor: pointer;
    position: relative;
}

.btn:hover {
    transition-duration: 0.1s;
    background-color: rgba(255,186,18, 0.8) ;
}

.btn:after {
    content: "";
    display: block;
    position: absolute;
    border-radius: 2px;
    left: 0;
    top:0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.5s;
    box-shadow: 0 0 10px 40px white;
}

.btn:active:after {
    box-shadow: 0 0 0 0 white;
    position: absolute;
    border-radius: 2px;
    left: 0;
    top:0;
    opacity: 1;
    transition: 0s;
}

.btn:active {
    top: 1px;
}
.inputContainer input:focus ~ label,
.inputContainer input:valid ~ label {
    top: -12px;
    font-size: 12px;

}

/* Customize the label (the container) */
.container {
    display: block;
    position: relative;
    padding-left: 35px;
    /*margin-bottom: 12px;*/
    cursor: pointer;
    font-size: 16px;
    color: white;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 16px;
    width: 16px;
    background-color: white;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
    background-color: white;
}

/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
    background-color: #ffba12;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
    left: 4px;
    top: 0px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    /*-webkit-transform: rotate(45deg);*/
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}
